import uniCalendar from "@/components/uni-calendar/uni-calendar.vue";
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue";
export default {
  components: {
    uniCalendar,
    uniNumberBox
  },
  data () {
    return {
      // 日历的开始日期
      startData: '',
      endData: '2099-12-29',
      // 成人人数
      adultNum: null,
      // 儿童人数
      childrenNum: null,
      // 选择的日期
      fulldate: '',
      // 页面传递过来的 id 值
      ProductId: '',
      // 红点日期
      selected: '',
      // 支付金额
      MaxShowPrice: ''
    };
  },
  methods: {
    // 当日期发生改变的时候
    change (e) {
      var data = e.fulldate
      for (var i = 0; i < this.selected.length; i++) {
        var choice = this.selected[i].date
        // console.log(choice)
        if (data == choice) {
          this.fulldate = choice
          return
        }
      }
      for (var i = 0; i < this.selected.length; i++) {
        var choice = this.selected[i].date
        if (data !== choice) {
          this.fulldate = ''
          uni.showToast({
            title: '请选择带有红点的日期',
            duration: 2000,
            icon: 'none',
            mask: true
          });
          return
        }
      }
    },
    // 成人输入框内容发生变化的时候
    adultChange (e) {
      this.adultNum = parseInt(e)
    },
    // 儿童输入框内容发生变化的时候
    childrenChange (e) {
      this.childrenNum = parseInt(e)
    },
    // 去填写订单信息页面
    goFillOrder () {
      var total = this.adultNum + this.childrenNum
      if (total < 10 && this.fulldate) {
        uni.navigateTo({
          url: '/pages/fillOrder/fillOrder?adultNum=' + this.adultNum + '&childrenNum=' + this.childrenNum + '&ProductId=' + this.ProductId + '&fulldate=' + this.fulldate + '&MaxShowPrice=' + this.MaxShowPrice
        });
      } else if (total >= 10 && this.fulldate) {
        uni.navigateTo({
          url: '/pages/company/company'
        });
      } else {
        uni.showToast({
          title: '请选择带有红点的日期',
          duration: 2000,
          icon: 'none'
        });
      }
    },
    // 获取当前日期
    getTime () {
      var date = new Date(),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
        minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
        second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      month >= 1 && month <= 9 ? (month = "0" + month) : "";
      day >= 0 && day <= 9 ? (day = "0" + day) : "";
      var timer = year + '-' + month + '-' + day
      this.startData = timer
      return timer;
    },
    // 获取出发时间
    GetProductOpenDateByProductID () {
      this.$http.post('/api/WeiXinApplet.ashx', {
        action: 'GetProductOpenDateByProductID',
        productId: this.ProductId
      }).then(res => {
        // console.log(res)
        if (res.data.status == true) {
          var result = []
          for (var i = 0; i < res.data.Data.Data.length; i++) {
            var str = {
              date: res.data.Data.Data[i].OpenDate
            }
            result.push(str)
          }
          this.selected = result
        }
      })
    }
  },
  onShow () {
    this.getTime()
  },
  onLoad (options) {
    console.log(options)
    this.ProductId = options.productId
    if (this.ProductId != undefined) {
      // 获取出发时间
      this.GetProductOpenDateByProductID()
    }
    this.MaxShowPrice = options.MaxShowPrice
  }
};
